.preloader{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: black;
    z-index: 999;
    opacity: 0.9;
    transition: 1s all;
    }
.loader{
    position: absolute;
    top: 50%;
    left: 50%;
}
.done{
    opacity: 0;
    visibility: hidden;
}